import { getHotSinger, getArtists } from "@/api/api.js";
export default {
  data() {
    return {
      singers: []
    };
  },

  beforeCreate() {},

  created() {
    this._getHotSinger();
  },

  beforeMount() {},

  mounted() {},

  beforeUpdate() {},

  updated() {},

  beforeDestroy() {},

  destroyed() {},

  methods: {
    _getHotSinger() {
      getHotSinger().then(res => {
        this.singers = res.artists;
      });
    },
    selectItem(data) {
      const id = data.id;
      getArtists(id).then(res => {});
    }
  },
  computed: {},
  components: {}
};



